* {
  padding: 0;
  margin: 0;
}
.App {
  padding: 10%;
  position: absolute;
  top: 50%;
  left: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  transform: translate(-50%, -50%);
  background-color: green;
  height:500px;
  width: 500px;
}
.App h1 {
  font-family: sans-serif;
  color: white;
}
